// Custom fonts
@font-face {
	font-family: 'VisbyCF Medium';
	src: url('/assets/visby-cf/visbycf-medium-webfont.woff2') format('woff2'), url('/assets/visby-cf/visbycf-medium-webfont.woff2') format('woff');
	font-weight: 400;
}

@font-face {
	font-family: 'VisbyCF Bold';
	src: url('/assets/visby-cf/visbycf-bold-webfont.woff2') format('woff2'), url('/assets/visby-cf/visbycf-bold-webfont.woff2') format('woff');
	font-weight: 700;
}

@font-face {
	font-family: 'VisbyCF ExtraBold';
	src: url('/assets/visby-cf/visbycf-extrabold-webfont.woff2') format('woff2'), url('/assets/visby-cf/visbycf-extrabold-webfont.woff2') format('woff');
	font-weight: 900;
}

//Typographic styles and touches
html {
	font-family: $font-stack-normal;
	color: $off-black;
	font-size: 18px;
	//	Nicer appearence of font
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
}

strong {
	font-weight: 700;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
span,
blockquote {
	font-weight: 100;
	max-width: 600px;
}